import React from 'react';
import {Link} from 'react-router-dom';

import {useDispatch} from 'react-redux';

import {startLogin} from '../actions/auth';

import {useForm} from '../hooks/useForm';

import './styles/Login.css';

export const Login = () => {

    const [formLoginValues, handleLoginInputChange] = useForm({
        lEmail: "",
        lPassword: ""
    });

    const {lEmail, lPassword} = formLoginValues;

    const dispatch = useDispatch();

    const handleLogin = (e) => {
        e.preventDefault();
        dispatch(startLogin(lEmail, lPassword));
    }



    return (
        <div className="login">
            <header className="header-login">
                <h1>Iniciar Sesión</h1>
            </header>
            <form onSubmit={handleLogin}>
                <input type="email" name="lEmail" placeholder="Email" value={lEmail} onChange={handleLoginInputChange} required/>
                <input type="password" name="lPassword" placeholder="Password" value={lPassword} onChange={handleLoginInputChange} required/>

                <button type="submit">Iniciar Sesión</button>
            </form>
            <Link to="/register">¿No tienes una cuenta?</Link>
        </div>
    )
}
