import React from 'react';
import { useDispatch } from 'react-redux';
import { todoStartDelete, todoStartUpdate } from '../actions/todo';
import trash from './assets/trash.png';

import './styles/Todo.css';

export const Todo = ({task, todoId, user, completed}) => {

    const dispatch = useDispatch();

    const handleUpdated = (todoId) => {
        dispatch(todoStartUpdate(user, todoId, completed));
    }

    const handleDeleted = (todoId) => {
        dispatch(todoStartDelete(user, todoId));
    }

    return (
        <li className="animate__animated animate__fadeIn">
            <p  onClick={() => handleUpdated(todoId)} className={(completed ? "completed" : "")}>{task}</p>
            <i><img src={trash} alt="trash icon" onClick={() => handleDeleted(todoId)}/></i>
        </li>
    )
}
