import React, { useEffect } from 'react'
import {BrowserRouter as Router, Switch, Redirect} from 'react-router-dom';

import {useDispatch, useSelector} from 'react-redux';

import {PublicRoute} from './PublicRoute';
import {PrivateRoute} from './PrivateRoute';

import {Landing} from '../components/Landing';
import {Login} from '../components/Login';
import {Register} from '../components/Register';
import {Todos} from '../components/Todos';
import {Loader} from '../components/Loader';
import { startChecking } from '../actions/auth';


export const AppRouter = () => {

    const dispatch = useDispatch();
    const {checking, uid} = useSelector(state => state.auth);

    useEffect(() => {
        dispatch(startChecking());
    }, [dispatch]);

    if(checking){
        return <Loader/>
    }

    return (
        <Router>
            <div>
                <Switch>
                    <PublicRoute exact path="/" component={Landing} isAuthenticated={!!uid}/>
                    <PublicRoute exact path="/login" component={Login} isAuthenticated={!!uid}/>
                    <PublicRoute exact path="/register" component={Register} isAuthenticated={!!uid}/>

                    <PrivateRoute exact path="/todos" component={Todos} isAuthenticated={!!uid}/>

                    <Redirect to="/"/>
                </Switch>
            </div>
        </Router>
    )
}

