import React from 'react';
import { useDispatch } from 'react-redux';
import {Link} from 'react-router-dom';
import { startRegister } from '../actions/auth';

import {useForm} from '../hooks/useForm';

import './styles/Register.css';

export const Register = () => {

    const [formRegisterValues, handleRegisterInputChange] = useForm({
        rName: "",
        rEmail: "",
        rPassword: ""
    });

    const dispatch = useDispatch();

    const {rName, rEmail, rPassword} = formRegisterValues;

    const handleRegister = (e) => {
        e.preventDefault();
        dispatch(startRegister(rEmail, rPassword, rName));
    }

    return (
        <div className="register">
            <header className="header-register">
                <h1>Registrarse</h1>
            </header>
            <form onSubmit={handleRegister}>
                <input type="text" name="rName" placeholder="Nombre" value={rName} onChange={handleRegisterInputChange} required/>
                <input type="email" name="rEmail" placeholder="Email" value={rEmail} onChange={handleRegisterInputChange} required/>
                <input type="password" name="rPassword" placeholder="Password" value={rPassword} onChange={handleRegisterInputChange} required/>

                <button type="submit">Registrate</button>
            </form>
            <Link to="/login">¿Ya tienes una cuenta?</Link>
        </div>
    )
}
