import {types} from '../types/types';
import {fetchConToken} from '../helpers/fetch';


export const todoStartAddNew = (event) => {
    return async(dispatch) => {

        const todo = {
            task: event.title,
        }

        try{
            const resp = await fetchConToken(`todos/${event.uid}`, todo, "POST");
            const body = await resp.json();

            if(body.ok){
                dispatch(todoAddNew(body.todo));
            }

        }catch(e){
            console.error(e);
        }
    }
}


const todoAddNew = (event) => ({
    type: types.todoAddNew,
    payload: event,
});

export const todoStartLoading = (id) => {
    return async (dispatch) => {
        try {
            const resp = await fetchConToken(`todos/${id}`);
            const body = await resp.json();

            const todos = body.todos;

            dispatch(todoLoaded(todos));



        }catch(e){
            console.error(e);
        }
    }
} 

const todoLoaded = (events) => ({
    type: types.todoLoaded,
    payload: events,
}) 

export const todoStartUpdate = (id, todoId, completed) => {
    return async(dispatch) => {
        try {
            const todo = {
                completed: !completed,
            }

            const resp = await fetchConToken(`todos/${id}/${todoId}`, todo, "PUT");
            const body = await resp.json();
            const newTodo = body.todo;

            console.log(newTodo);

            if(body.ok){
                dispatch(todoUpdated(newTodo))
            }

        }catch(e){
            console.error(e);
        }
    }
}

const todoUpdated = (event) => ({
    type: types.todoUpdated,
    payload: event,
})

export const todoStartDelete = (id, todoId) => {
    return async(dispatch) => {
        try{

            const resp = await fetchConToken(`todos/${id}/${todoId}`, {}, "DELETE")
            const body = await resp.json();

            if(body.ok){
                dispatch(todoDeleted(todoId));
            }

            console.log(todoId);

        }catch(e){
            console.error(e);
        }
    }
}

const todoDeleted = (event) => ({
    type: types.todoDeleted,
    payload: event,
})

export const clearTodo = () => ({
    type: types.todoClear
})